<script>
import loadingComponent from './components/loading/loading-component';
import API from "./services/landAPI";
import questionsList from './components/questions-list/questions-list'

export default {
  name: 'app',
  data: function() {
    return {
      currentTime: null,
      currentTimestart: null,
      curentdate: new Date(),
      timer: null,
      countryCode: '07',
      phone: '',
      progress: 20,
      totalTime: (3 * 60),
      buttonNext: true,
      phoneMask: '+36-99-999-9999',
      loadingModal: false,
      showLastPage: false,
      view: 0,
      key: '',
      QUERY: this.getQuery(window.location.search),
      click_id: null
    }
  },
  components: {
    loadingComponent,
    questionsList
  },
  methods: {
    maskCheck: function (field){
      if (field.target.inputmask.isComplete()) {
        this.buttonNext = false;
      } else {
        this.buttonNext = true;
      }
    },
    startTimer: function() {
      this.timer = setInterval(() => this.countdown(), 1000);
    },
    padTime: function(time) {
      return (time < 10 ? '0' : '') + time;
    },
    countdown: function() {
      if(this.totalTime >= 1){
        this.totalTime--;
      } else{
        this.totalTime = 0;
      }
    },
    next: function() {
      this.loadingModal = !this.loadingModal;
    },
    clearNumber (num) {
        return num.replace(/\D+/g, '');
    },
    timeOutStop: function (data) {
      if ( data ) {
        this.upView();
        this.loadingModal = false;
        this.startTimer();
      }
    },
    upView() {
      this.view ++;
    },
    keyGenerate(length) {
      let text = "";
      let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

      for (let i = 0; i < length; i++) {
          text += possible.charAt(Math.floor(Math.random() * possible.length));
      }

      return text;
    },
    getQuery(string) {
      return string.slice(1).split("&")
          .map((queryParam) => {
              let kvp = queryParam.split("=");
              return { key: kvp[0], value: kvp[1] }
          })
          .reduce((query, kvp) => {
              query[kvp.key] = kvp.value;
              return query
          }, {})
    },
    sendNumber () {
      let phone = this.clearNumber(this.phone);
      let resp = null;
      API.getSHA(phone, this.click_id, this.key)
              .then(response => ( resp = response ))
              .catch(error => console.log(error));
    }
  },
  computed: {
    minutes: function() {
      const minutes = Math.floor(this.totalTime / 60);
      return this.padTime(minutes);
    },
    seconds: function() {
      const seconds = this.totalTime - (this.minutes * 60);
      return this.padTime(seconds);
    }
  },
  created() {
      if ( !this.QUERY.click_id ) {
          this.key = this.keyGenerate(12);
          let data = JSON.stringify(this.QUERY);
          let ths = this;
          API.getID(data, this.key)
              .then(response => (ths.click_id = response.data))
              .catch(error => console.log(error));
      } else {
          this.click_id = this.QUERY.click_id;
      }
  }
}
</script>

