  body, html {
    font-size: 11px;
    font-family: Roboto, sans-serif
  }

  .wrapper {
    max-width: 350px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    height: 80%;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center
  }

  .wrapper-header {
    background-color: #1a2842;
    width: 100vw
  }

  .header {
    height: 100vh
  }

  .header-icon {
    margin-top: 5px;
    display: inline-block
  }

  .header-top {
    height: 67px;
    width: 93%;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap
  }

  .header__text {
    color: #fff;
    line-height: 1.16rem;
    font-size: 11px
  }

  .header__text span {
    color: #2f9e31
  }

  .logo {
    margin-right: 10px
  }

  .main {
    height: 85vh;
    width: 85%;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: distribute;
    justify-content: space-around
  }

  .main-suptitle__inner {
    color: #1a2842;
    font-size: 1.08rem;
    line-height: 1.33rem
  }

  .main-suptitle__inner span {
    font-weight: 700
  }

  .main-title__inner {
    color: #9e2f2f;
    text-transform: uppercase;
    font-weight: 900;
    line-height: 2.5rem;
    font-size: 25px
  }

  .main-title__inner-text {
    color: #2f9e31;
    text-transform: uppercase;
    font-weight: 900;
    line-height: 2.5rem;
    font-size: 25px
  }

  .main-text-subtitle__inner {
    text-transform: uppercase;
    font-size: 2.16rem;
    line-height: 2.5rem;
    font-weight: 700
  }

  .main-text__inner {
    color: #1a2842;
    font-size: 1.66rem;
    line-height: 2.16rem
  }

  .main-text__inner-text {
    font-size: 1.4rem;
    font-weight: 700;
    color: #9e2f2f
  }

  .main-small__inner {
    font-weight: 900;
    color: #fff;
    line-height: 1.63rem;
    font-size: 1.3rem;
    padding: 10px 15px;
    background: #1a2842;
    border: 1px solid #000
  }

  .main-open__inner {
    color: #bd3434;
    font-size: 1.33rem;
    line-height: 1.66rem;
    font-weight: 900
  }

  .button {
    margin: 10px 0;
    position: relative
  }

  .button__inner {
    width: 100%;
    background: #2f9e31;
    box-sizing: border-box;
    position: relative;
    text-decoration: none;
    border-radius: 3px;
    padding: 17px 0 17px 40px;
    text-align: center;
    display: block;
    font-size: 14.5px;
    border: none;
    color: #fff;
    letter-spacing: .1em;
    font-weight: 900;
    &.no-img {
      padding: 17px 0;
    }
  }

  .button__inner:disabled {
    background: grey
  }

  .warning {
    color: #ce1126;
    text-transform: uppercase;
    font-size: 1.23rem;
    line-height: 1.91rem
  }

  .icon-call {
    z-index: 9999;
    position: absolute;
    left: 45px;
    top: 0;
    bottom: 0;
    right: auto;
    margin: auto;
    transform: translateY(-1%);
    animation: 1.2s ease 0s normal none 1 running swing;
    animation-iteration-count: infinite;
    -webkit-animation: 1.2s ease 0s normal none 1 running swing;
    -webkit-animation-iteration-count: infinite
  }

  #summary-footer, .section-copyright {
    font-size: 11px;
    position: absolute;
    width: 100%;
    margin: 0 auto;
    right: 0;
    left: 0;
    text-align: center;
    width: 90%;
    color: #bcbcbc
  }

  .footer p {
    text-align: center;
    width: 90%;
    color: #bcbcbc
  }

  .currentTime {
    color: red
  }

  @media (min-width: 1024px) {
    .header-top {
      max-width: 350px
    }
  }

  @media (min-width: 768px) and (orientation: portrait) {
    body, html {
      font-size: 15px
    }
  }

  @media (orientation: landscape) {
    .warning__inner {
      margin-bottom: 15px
    }
    .header {
      height: auto
    }
    .main {
      height: 100%;
      min-height: 600px
    }
  }

  @media (max-width: 320px) {
    .button__inner {
      font-size: 12px
    }
    .icon-call {
      left: 40px
    }
  }

  @media (max-width: 320px) and (max-height: 480px) {
    .warning {
      margin-bottom: 0
    }
    body, html {
      font-size: 11px
    }
  }

  @media (max-width: 374px) and (max-height: 548px) {
    .wrapper {
      height: auto
    }
  }
  .phone-input {
    border: 6px solid #aeaeaf;
    border-radius: 5px;
    padding: 10px 20px;
    line-height: 1;
    outline: none;
    width: 100%;
    box-sizing: border-box;
  }
</style>
